import React, { Fragment, useState } from "react";
import { useHistory } from "react-router-dom";
import Modal from "react-modal";
import FormPrograma from "./modals/FormPrograma";
import Reports from "./modals/Reports";
import ProgramService from "../services/program-service";

const customStyles = {
  content: {
    width: "75%",
    height: "100%",
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
    backgroundColor: "rgb(58, 58, 58)",
  },
};

Modal.setAppElement("#main");

const HorarioController = (props) => {
  //let subtitle;
  const history = useHistory();
  const programService = new ProgramService();

  const [value, setValue] = useState({
    ann: {},
    form: {
      name: "",
      color: "#563d7c",
      announcer: [],
      moderator: "",
      day: 1,
      hourIni: "00",
      hourFin: "00",
    },
    report: "",
  });

  const handleChange = (e) => {
    setValue({
      form: {
        ...value.form,
        [e.target.name]: e.target.value,
      },
    });
  };

  const handleSelectAnnouncer = (e) => {
    setValue((value) => {
      const announcer = [...value.form.announcer, e];

      return {
        form: {
          ...value.form,
          announcer,
        },
      };
    });
  };

  const handlePopAnnouncer = (e) => {
    setValue((value) => {
      const index = value.form.announcer.indexOf(e);

      const announcer = [
        ...value.form.announcer.slice(0, index),
        ...value.form.announcer.slice(index + 1),
      ];

      return {
        form: {
          ...value.form,
          announcer,
        },
      };
    });
  };

  const handleSubmit = async (e) => {
    console.log(value.form);
    const confirm = await programService.addProgram(value.form);
    if (confirm.err) {
      console.log("Error al obtener la data");
      return;
    }
    console.log(confirm);
    closeModal();
    history.push("/");
    e.preventDefault();
  };

  //handleChange = handleChange.bind(this)
  //handleSubmit = handleSubmit.bind(this)

  const [modalIsOpen, setIsOpen] = useState(false);

  const openModal = () => {
    setIsOpen(true);
  };

  const afterOpenModal = () => {
    //subtitle.style.color = '#f00';
  };

  const closeModal = () => {
    setIsOpen(false);
  };

  const [modalReportOpen, setReportOpen] = useState(false);

  const openReport = () => {
    setReportOpen(true);
  };

  const closeReport = () => {
    setReportOpen(false);
  };

  const exportReports = async (e) => {
    await programService
      .readReports()
      .then((data) => {
        let s = "";
        data.forEach((e) => {
          s += e.moderatorReport + "\n\n\n";
        });
        setValue({
          form: {
            ...value.form,
          },
          report: s,
        });
      })
      .then(() => {
        openReport();
      });
  };

  const cleanReports = async (e) => {
    await programService
      .cleanReports()
      .then((data) => {
        console.log(data);
        let s = "Reportes limpios";
        setValue({
          form: {
            ...value.form,
          },
          report: s,
        });
      })
      .then(() => {
        openReport();
      });
  };

  return (
    <Fragment>
      <div className="d-flex justify-content-around w-100">
        <button
          type="button"
          className="btn btn-danger"
          data-bs-toggle="modal"
          data-bs-target="#confirmModal"
        >
          Limpiar Reportes
        </button>
        <button onClick={exportReports} type="button" className="btn btn-info">
          Exportar Reportes
        </button>
        <button onClick={openModal} type="button" className="btn btn-warning">
          Añadir Programa
        </button>
      </div>

      <div
        className="modal fade"
        id="confirmModal"
        tabindex="-1"
        aria-labelledby="confirmModalLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title text-center mx-auto" id="confirmModalLabel">
                ¿Segur@ que desea eliminar todos los reportes?
              </h5>
            </div>
            <div className="modal-footer d-flex justify-content-center">
              <button
                type="button"
                className="btn btn-primary"
                onClick={cleanReports}
                data-bs-dismiss="modal"
              >
                Aceptar
              </button>
              <button
                type="button"
                className="btn btn-secondary"
                data-bs-dismiss="modal"
              >
                Cancelar
              </button>
            </div>
          </div>
        </div>
      </div>

      <Modal
        isOpen={modalReportOpen}
        onRequestClose={closeReport}
        style={customStyles}
        contentLabel="Reports"
      >
        <Reports report={value.report} closeReport={closeReport}></Reports>
      </Modal>

      <Modal
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Example Modal"
      >
        <FormPrograma
          form={value.form}
          closeModal={closeModal}
          handleChange={handleChange}
          handleSelectAnnouncer={handleSelectAnnouncer}
          handleSubmit={handleSubmit}
          handlePopAnnouncer={handlePopAnnouncer}
        ></FormPrograma>
      </Modal>
    </Fragment>
  );
};

export default HorarioController;
