import React, { Component } from "react";

export default class Reports extends Component {
  constructor(props) {
    super(props);

    this.state = {};
  }

  render() {
    let { report } = this.props;

    const button = {
      width: 'auto',
      height: '38px'
    }

    return (
      <div className="d-flex flex-column">
        <textarea
          className="form-control"
          value={report}
          rows="21"
          readOnly
        ></textarea>
        <button
          onClick={this.props.closeReport}
          type="button"
          className="btn btn-danger mt-3 mx-auto"
          style={button}
        >
          Cerrar
        </button>
      </div>
    );
  }
}
